<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>代码分析</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" style="margin-top: 20px;">
      <div class="app">
        <div slot="header" class="clearfix">
          <div>代码分析工具</div>
          <el-button style="float: right; padding: 3px 0" type="text">X</el-button>
        </div>

        <div class="button-box">
          <el-button size="mini"> 开始 </el-button>
          <el-button size="mini" @click="check = false"> 编辑 </el-button>
          <el-dropdown style="margin: 0 10px;" @command="handleCommand">
            <el-button size="mini"> 项目 </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="/project">项目管理</el-dropdown-item>
              <el-dropdown-item command="/problem">问题管理</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button size="mini" @click="handleHelp"> 帮助</el-button>
          <el-button size="mini" @click="() => { this.$router.push('/report') }"> 报告 </el-button>
        </div>

        <div class="conten-box">
          <el-row style="height: 100%;box-sizing: border-box;">
            <el-col :span="6" style="height: 100%;box-sizing: border-box;">
              <el-card shadow="never" style="height: 100%;box-sizing: border-box;">
                <el-tree ref="tree" :data="data" :props="defaultProps" node-key="id" highlight-current
                  :default-expanded-keys="['3']">
                </el-tree>

              </el-card>
            </el-col>
            <el-col :span="17" :offset="1" style="height: 100%;box-sizing: border-box;">
              <el-input type="textarea" v-model="code" rows="20" :disabled="check"></el-input>

              <div class="footer" style="position: absolute;bottom: 20px;width: 200px;padding-left: 20px;"
                v-show="check == false">
                <!-- <el-button size='small' @click="() => { this.$router.push('/report') }">查看报告</el-button> -->
                <el-button size='small' type="primary" @click="analyzeCode">保存</el-button>
                <el-button size='small' @click="check = true">返回</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>


    </el-card>
  </div>
</template>
<script>

export default {
  name: "index",
  data() {
    return {
      isAnalyzed: true,
      importedCode: '',
      reportData: {

      },

      data: [{
        id: '1',
        label: '项目一',
        children: [{
          id: '2',
          label: 'file',
          children: [{
            id: '3',
            label: 'code',
          }]
        }]
      }
      ],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      check: true,
      code: `linclude <stdio.h>int main ()
{
#include "stdio.h"include "math.h"linclude "windows.h"
ltinclude "time.h"使用当前时钟做种子
define N 15l定义棋盘大小1黑2白#define MN+8l定义数据棋盘大小
int chess[N][N];/棋盘，用于储存下子位置
int count[M][M];//数据棋盘，用于储存下子位置，和盘外数据
char buffer[N*2-1][N*4-3];//缓冲器，用于储存下子位置，盘外数据，和棋盘符号long sum[M][M]= H0};/得分棋盘，储存得分情况
int p[20][5];//存储该位置的所有可能的五连珠情况
int player = 1, ai = 2, error =-1;
int num;/双方回合总数
int now,//设置下棋标志int flag0;/确定己方已下子int gs,/游戏结束标志
# -*- coding=utf-8 -*-
import random
import pygame
pygame.init()
space = 60 # 四周留下的边距
cell_size = 40 # 每个格子大小
cell_num = 15
grid_size = cell_size * (cell_num - 1) + space * 2 # 棋盘的大小
screencaption = pygame.display.set_caption('FIR')
screen = pygame.display.set_mode((grid_size,grid_size)) #设置窗口长宽
while True:
for event in pygame.event.get():
if event.type == pygame.QUIT:
pygame.quit()
exit()
screen.fill((0,0,150)) # 将界面设置为蓝色
for x in range(0,cell_size*cell_num,cell_size):
pygame.draw.line(screen,(200,200,200),(x+space,0+space),(x+space,cell_size*(cell_num-1)+space),1)
for y in range(0,cell_size*cell_num,cell_size):
pygame.draw.line(screen,(200,200,200),(0+space,y+space),(cell_size*(cell_num-1)+space,y+space),1)
pygame.display.update() # 必须调用update才能看到绘图显示
`

    };
  },
  created() {
    this.$nextTick(() => {
      this.$refs.tree.setCurrentKey('3')
    })
  },
  methods: {
    saveCode() {
      // 在这里实现保存代码的逻辑，例如保存到数据库或本地文件。  
      console.log('保存代码:', this.importedCode);
    },
    analyzeCode() {
      // 在这里实现分析代码的逻辑，例如调用代码分析工具。  
      // 假设分析成功，设置 isAnalyzed 为 true 并获取报告数据。  
      // this.isAnalyzed = true;
      this.$confirm('请确认是否分析, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '保存成功!'
          });
          this.check = true;
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    viewReport() {
      // 这里只是简单地显示报告数据。  
      console.log('查看报告:', this.reportData);
    },
    editCode() {
      // 实现代码编辑功能逻辑，例如打开一个代码编辑器。  
      console.log('编辑代码:', this.importedCode);
    },
    handleCommand(url) {
      this.$router.push(url);
    },
    handleHelp() {
      this.$message({
        message: '如果您有任何问题或需要进一步的帮助，请随时联系我们。',
        type: 'info'
      });
    }


  },
  mounted() {
    // 在组件挂载后，如果已经有保存的代码，自动进行分析。  
    // 设置首行高亮效果
  },
};  
</script>

<style scoped lang="scss">
.el-card {
  border-color: rgb(220, 223, 230);
}

.app {
  height: 600px;
  display: flex;
  flex-flow: column;


  .button-box {
    .el-button {
      width: 100px;
    }
  }

  .conten-box {
    margin-top: 20px;
    flex: 1;



  }

}
</style>